import React, { Component } from 'react';

import {bindActionCreators} from  'redux';
import {connect} from 'react-redux';

import { getParamsUrl } from '../utils';

import "./WebPage.less";
import { storeRatePage } from "../store/RatePage";
import { NavBar, Icon, Card, WhiteSpace, Grid} from 'antd-mobile';

const { getFollowers, getGroupById, getFollowerById, regVKApp, vkWebAppGetUserInfo } = storeRatePage;

class WebPage extends Component{
    componentDidMount() {
        var groupId = getParamsUrl("vk_group_id");
        var userId = getParamsUrl("vk_user_id");

        this.props.regVKApp();
        this.props.vkWebAppGetUserInfo();
        this.props.getFollowers(groupId);
        this.props.getGroupById(groupId);
        this.props.getFollowerById(groupId, userId);
    }
    render() {
        const { followers, group, myAccount } = this.props;
        return (
            <div>
                <NavBar
                    mode="dark"
                >
                    Рейтинг
                </NavBar>
                {
                    group ?
                        <div>
                            <h2 style={{ textAlign: "center" }}></h2>
                            <Card full>
                                <Card.Header
                                    title={"Название"}
                                    thumb={""}
                                    thumbStyle={{
                                        "border-radius": "50%",
                                        "width": "50px"
                                    }}
                                    extra={
                                        <div style={{ "float": "right" }} class="am-card-header-content">
                                            <img src="https://img.icons8.com/color/32/000000/coins.png" />
                                            {100}
                                        </div>
                                    }
                                />
                                <Card.Body>
                                    {
                                        myAccount ?
                                            <Grid className={"rate"} columnNum={4} data={[
                                                {
                                                    text: 101,
                                                }, {
                                                    icon: 'https://img.icons8.com/color/96/000000/filled-like.png',
                                                    text: 0,
                                                }, {
                                                    icon: 'https://img.icons8.com/color/96/000000/filled-topic.png',
                                                    text: 0,
                                                }, {
                                                    icon: 'https://img.icons8.com/color/96/000000/undo.png',
                                                    text: 0,
                                                },
                                            ]} activeStyle={false} />
                                            : <h3 style={{ textAlign: "center" }}>Нет активных действий</h3>
                                    }
                                </Card.Body>
                            </Card>
                            <h2 style={{ textAlign: "center" }}>Группа</h2>
                            <Card full>
                                <Card.Header
                                    title={group.name}
                                    thumb={group.photo100}
                                    thumbStyle={{
                                        "border-radius": "50%"
                                    }}
                                    extra={
                                        <div style={{ "float": "right" }} class="am-card-header-content">
                                            <img src="https://img.icons8.com/color/32/000000/coins.png" />
                                            {100}
                                        </div>
                                    }
                                />
                            </Card>
                            <h2 style={{ textAlign: "center" }}>Рейтинг подписчиков</h2>
                            {
                                followers.map((f, i) => <div>
                                    <WhiteSpace size="sm" />
                                    <Card full>
                                        <Card.Header
                                            title={f.name}
                                            thumb={f.photo100}
                                            thumbStyle={{
                                                "border-radius": "50%",
                                                "width": "50px"
                                            }}
                                            extra={
                                                <div style={{ "float": "right" }} class="am-card-header-content">
                                                    <img src="https://img.icons8.com/color/32/000000/coins.png" />
                                                    {f.balls}
                                                </div>
                                            }
                                        />
                                        <Card.Body>
                                            <Grid className={"rate"} columnNum={4} data={[
                                                {
                                                    text: f.num,
                                                }, {
                                                    icon: 'https://img.icons8.com/color/96/000000/filled-like.png',
                                                    text: f.likes,
                                                }, {
                                                    icon: 'https://img.icons8.com/color/96/000000/filled-topic.png',
                                                    text: f.comments,
                                                }, {
                                                    icon: 'https://img.icons8.com/color/96/000000/undo.png',
                                                    text: f.reposts,
                                                },
                                            ]} activeStyle={false} />
                                        </Card.Body>
                                    </Card>
                                </div>
                                )
                            }
                        </div> : <h1 style={{ paddingTop: 200, "text-align": "center" }}>
                            Нет информации о данной группе
                        </h1>
                    }
            </div>
        );
	}
};
function mapStateToProps (state) {
    return {
        followers:state.ratePage.followers,
        group: state.ratePage.group,
        myAccount: state.ratePage.myAccount
    };
}
function matchDispatchToProps(dispatch){ 
    return bindActionCreators({ getFollowers, getGroupById, getFollowerById, regVKApp, vkWebAppGetUserInfo },dispatch);
}
export default connect(mapStateToProps,matchDispatchToProps)(WebPage);
